
<style scoped lang='scss'>
// 
.decide {
  &-avatar {
    &-camera {
          padding: 1.33333vw 4vw;
          background: #fff;
          height: 12.26667vw;
          line-height: 12.26667vw;
          font-size: 4.53333vw;
          font-weight: 300;
          color: #030303;
          text-align: center;
          display: block;
          justify-content: space-between;
          position: relative;
          input {
            position: absolute;
            top: 0;
            left: 0;
            opacity: 0;
            color: inherit;
            font: inherit;
          }
          &:after {
            content: "";
            display: block;
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            width: 100%;
            height: 1px;
            background-color: #f5f5f5;
          }
    }
    &-local {
          padding: 1.33333vw 4vw;
          background: #fff;
          height: 12.26667vw;
          line-height: 12.26667vw;
          font-size: 4.53333vw;
          font-weight: 300;
          color: #030303;
          text-align: center;
          display: block;
          justify-content: space-between;
          position: relative;
          input {
            position: absolute;
            top: 0;
            left: 0;
            opacity: 0;
            color: inherit;
            font: inherit;
          }
          &:after {
            content: "";
            display: block;
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            width: 100%;
            height: 1px;
            background-color: #f5f5f5;
          }
    }
  }
  &-grade {

  }
  &-birth {

  }
  &-area {

  }
}
.avatar-setter {
    background: rgba($color: #000000, $alpha: 0.5);
}
.infosetterbox {
  height: 100%;
  background: #f0f2f5;
}
.infolist {
  background: #fff;
  margin: 2.66667vw 0;
  padding: 1.33333vw 4vw;

  &-cell {
    height: 14.66667vw;
    line-height: 14.66667vw;
    display: flex;
    justify-content: space-between;
    position: relative;
    &:not(.mobile):after {
      content: "";
      display: block;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%) rotate(45deg);
      width: 2.66667vw;
      height: 2.66667vw;
      border-top: 1px solid #b7b7b7;
      border-right: 1px solid #b7b7b7;
    }
    span {
      &.one {
        font-size: 3.73333vw;
        color: #595959;
      }
      &.two {
        font-size: 3.73333vw;
        color: #8c8c8c;
        margin-right: 6.66667vw;
      }
      img {
        width: 9.86667vw;
        height: 9.86667vw;
        border-radius: 50%;
        vertical-align: middle;
      }
    }
  }
}
</style>


<template>
  <div class="infosetterbox">
    <van-nav-bar title="" left-arrow @click-left="onClickLeft" />
    <ul class="infolist" v-if="info">
      <li class="infolist-cell" @click="setinfo('')">
        <span class="one"></span>
        <span class="two">
          <img :src="info.avatar" />
        </span>
      </li>
      <li class="infolist-cell" @click="setinfo('')">
        <span class="one"></span>
        <span class="two">{{ info.nickname }}</span>
      </li>
      <li class="infolist-cell mobile">
        <span class="one"></span>
        <span class="two">{{ info.mobile }}</span>
      </li>
      <li class="infolist-cell" @click="setinfo('')">
        <span class="one"></span>
        <span class="two">{{ info.sex==3?'':info.sex?'':'' }}</span>
      </li>
      <li class="infolist-cell" @click="setinfo('')">
        <span class="one"></span>
        <span
          class="two"
        >{{ info.birthday?info.birthday:'' }}</span>
      </li>
      <li class="infolist-cell" @click="setinfo('')">
        <span class="one"></span>
        <span
          class="two"
        >{{ [info.province_name,info.city_name,info.district_name].filter(m=>{return m.trim()}).join(',')?'':'' }}</span>
      </li>
      <li class="infolist-cell" @click="setinfo('')">
        <span class="one"></span>
        <span class="two">{{ attr&&attr.length?attr[0].attr_value:'' }}</span>
      </li>
      <li class="infolist-cell" @click="setinfo('')">
        <span class="one"></span>
        <span class="two">{{ attr&&attr.length?attr[1].attr_value:'' }}</span>
      </li>
    </ul>
    <!--  -->
    <van-action-sheet 
      v-model="rise._avatar" 
      cancel-text=""  
      class="decide-avatar"
    >
        <div class="decide-avatar-camera">
            
            <input 
              @change="uploadimg('c',$event)" 
              ref="fromcamera"
              type="file"
               accept="image/*" 
              capture="camera" 
            />
        </div>
        <div class="decide-avatar-local">
            
            <input
              @change="uploadimg('l',$event)" 
              type="file" 
              accept="image/*" 
              ref="fromlocal"
            />
        </div>
    </van-action-sheet>

    <!--  -->
    <van-action-sheet
        v-model="rise._birth"
      class="decide"
    >
        <van-datetime-picker
            v-model="birthdata"
            type="date"
            :min-date="minDate"
            :max-date="maxDate"
            @confirm='decide_birth'
        />
    </van-action-sheet>


    <!--  -->
        <van-action-sheet
        v-model="rise._grade"
        cancel-text=""
        close-on-click-action
        @cancel="onCancel"
      class="decide-grade"
    >
      <van-picker
        title=""
        show-toolbar
        :columns="picker_options._grade"
        @confirm="grade_confirm"
        @cancel="grade_cancel"
        @change="grade_change"
      ></van-picker>
      </van-action-sheet>

    <!--  -->
    <van-action-sheet
      v-model="rise._area"
      cancel-text=""
      close-on-click-action
      @cancel="onCancel"
      class="decide"
    >
      <!--  -->
      <van-area title="" :area-list="areaList" />
    </van-action-sheet>

  </div>
</template>

v-for='(m,i) in attr?attr:[]' :key='i'
<script>
import area from "@/util/data_mod/area";
export default {
  data() {
    return {
      local_img:null,
      // 
      picker_options: {
        _avatar: ["", "", "", "", ""],
        _grade: ["", "", "", "", ""]
      },
      // 
      rise: {
        _avatar: false,
        _birth: false,
        _area: false,
        _grade: false
      },
      actions: [{ name: "" }, { name: "" }, { name: "" }],
      //   
      areaList: area,
      // 
      show: false,
      // 
      minDate: new Date(1970, 0, 1),
      maxDate: new Date(2025,11,30),
      birthdata: new Date(),
      info: null,
      attr: null,
      empty_attr: [
        { attr: "", attr_value: "" },
        { attr: "", attr_value: "" }
      ],
      infobox:{
        avatar:'',
        birthday:'',

      }
    };
  },
  created() {},
  mounted() {
    this.getuserinfo();
      this.getuserattr()
  },
  //props:{},//[]
  methods: {
    // 
    async decide_birth(v){
      let birthday=[
        v.getFullYear(),
        v.getMonth()+1,
        v.getDate()
      ].map(m=>{return (''+m).padStart(2,'0')}).join('-')
      this.infobox.birthday=birthday
      console.log(birthday)
      let {data}=await this.$http.put(
        `http://120.53.31.103:84/api/app/user`,{
          birthday
        }
      )
      console.log(data)
    this.getuserinfo();
    },
    uploadimg(key,e=event){
      this.local_img=e.target.files[0]
      let type=this.local_img.type.split('/')[0]
      if(type=='image'){
        let reader=new FileReader()
        reader.readAsDataURL(this.local_img)
        reader.onloadend=()=>{
          let dataURL=reader.result;
          let blob=this.dataURItoBlob(dataURL)
          this.upload_file(blob)
        }
      }else{
        console.log('')
      }
      this.rise._avatar=false
    },
    dataURItoBlob(dataURI){
      let byteString=atob(dataURI.split(',')[1]);
      let mimeString=dataURI.split(',')[0].split(':')[1].split(';')[0]
      let ab=new ArrayBuffer(byteString.length);
      let ia=new Uint8Array(ab)
      for(let i=0;i<byteString.length;i++){
        ia[i]=byteString.charCodeAt(i)
      }
      return new Blob([ab],{type:mimeString})
    },
    async upload_file(imgUrl){
      let formdata=new FormData()
      formdata.append('file',imgUrl)
      let {data:res}=await this.$http.post(
        `http://120.53.31.103:84/api/app/public/img`,formdata,{
          headers:{
            'Content-Type':'multipart/form-data'
          }
        }
      )
      
      console.log(res)
      this.infobox.avatar=res.data.path
      let {data}=await this.$http.put(
        `http://120.53.31.103:84/api/app/user`,{
          avatar:res.data.path
        }
      )
      console.log(data)
      this.getuserinfo();
    },
    grade_confirm() {
      console.log("  ");
    },
    grade_cancel() {
      console.log("  ");
    },
    grade_change() {
      console.log("  ");
    },
    onCancel() {},
    onClickLeft() {
      this.$router.go(-1);
    },
    setinfo(keystr) {
      switch (keystr) {
        case "":
          console.log(" " + keystr);
          this.rise._avatar = true;
          break;
        case "":
          console.log(" " + keystr);
          this.rise._birth = true;
          break;
        case "":
          console.log(" " + keystr);
          this.rise._area = true;
          break;
        case "":
          console.log(" " + keystr);
          this.rise._grade = true;
          break;
        default:
          console.log(" " + keystr);
          return;
          break;
      }
    },
    async getuserinfo() {
      let {
        data: { data: res }
      } = await this.$http.get(`http://120.53.31.103:84/api/app/userInfo`, {
        params: {}
      });
      // console.log(res)
      this.info = res;
      this.attr = res.attr;
      console.log(this.info, this.attr);
    },
    async getuserattr(){
      let {data:{data:res}}=await this.$http.get(
        `http://120.53.31.103:84/api/app/module/attribute/1?`,{
          params:{}
        }
      )
      console.log(res)
    }
  },
  computed: {},
  watch: {},
  filters: {},
  directives: {},
  components: {}
};
</script>